:host {
  display: block;
  margin: var(--gcds-header-margin) !important;

  slot {
    display: initial;
  }

  .gcds-header__container {
    justify-content: space-between;
    width: 90%;
    max-width: var(--gcds-header-container-max-width);
    margin: 0 auto;
  }

  .gcds-header__brand {
    border-block-end: var(--gcds-header-brand-border-width) solid
      var(--gcds-header-brand-border-color);
    padding: var(--gcds-header-brand-padding);
    margin: var(--gcds-header-brand-margin);

    .brand__container {
      display: grid;
      grid-gap: var(--gcds-header-brand-grid-gap);
      grid-template-columns: 1fr 0.1fr;
      grid-template-areas:
        'signature toggle'
        'search search';
      width: 90%;
      max-width: var(--gcds-header-container-max-width);
      margin: 0 auto;

      &.container--simple {
        grid-template-areas: 'signature toggle';
      }

      .brand__toggle,
      slot[name='toggle'] {
        grid-area: toggle;
        text-align: right;
      }

      .brand__signature,
      slot[name='signature'] {
        grid-area: signature;

        gcds-signature {
          margin: var(--gcds-header-brand-signature-margin);
        }
      }

      .brand__search {
        grid-area: search;
        max-width: 100%;
        display: block;
      }
    }

    @media screen and (min-width: 64em) {
      .brand__container {
        grid-template-areas:
          'toggle toggle'
          'signature search';
        grid-template-columns: 1fr 1fr;

        .brand__search,
        slot[name='search'] {
          width: fit-content;
          margin-inline-start: auto;
        }
      }
    }
  }

  .gcds-header__skip-to-nav {
    text-align: center;
    position: absolute;
    width: 100%;
    margin-inline: auto;
    top: var(--gcds-header-skiptonav-top);
  }
}
